import React from 'react';
import styled from '../../typed-components';
import { Link } from 'react-router-dom';
import { getPlaces, userProfile } from '../../types/api';
import { MutationFn } from 'react-apollo';
import Helmet from 'react-helmet';
import Header from '../../Components/Header';
import Place from '../../Components/Place';

const Container = styled.div`
  padding: 0px 40px;
`;

const Image = styled.img`
  height  :60px;
  width:60px;
  border-radius:50%;
`;

const GridLink = styled(Link)`
  display:grid;
  grid-template-columns:1fr 4fr;
  grid-gap:10px;
  margin-bototm:10px;
`;
const Keys = styled.div``;
const Key = styled.span`
  display:block;
  margin-bottom:5px;
`;

const FakeLink = styled.span`
  text-decoration:underline;
  cursor:pointer;
`;

const SLink = styled(Link)`
  display:block;
  text-decoration:underline;
  margin:20px 0px;
`;

interface IProps {
  logUserOut: MutationFn;
  userData?: userProfile;
  placesData?: getPlaces;
  userDataLoading: boolean;
  placesLoading: boolean;
}

const SettingPresenter: React.SFC<IProps> = ({
  logUserOut,
  userData: { GetMyProfile: { user = null } = {} } = {},
  placesData: { GetMyPlaces: { places = null } = {} } = {},
  userDataLoading,
  placesLoading
}) => (
    <React.Fragment>
      <Helmet>
        <title>Settings|Nuber</title>
      </Helmet>
      <Header title={"Account Settings"} backTo={"/"} />
      <Container>
        <GridLink to={"/edit-account"}>
          {!userDataLoading &&
            user &&
            user.email &&
            user.profilePhoto &&
            user.fullName && (
              <React.Fragment>
                <Image src={user.profilePhoto} />
                <Keys>
                  <Key>{user.fullName}</Key>
                  <Key>{user.email}</Key>
                </Keys>
              </React.Fragment>
            )

          }
        </GridLink>
        {!placesLoading &&
          places &&
          places.map(place => (
            <Place
              id={place!.id}
              key={place!.id}
              fav={place!.isFav}
              name={place!.name}
              address={place!.address}
            />
          ))
        }
        <SLink to={"/places"}>Go to places</SLink>
        <FakeLink onClick={logUserOut}>LogOut</FakeLink>

      </Container>
    </React.Fragment>
  );

export default SettingPresenter;


